// src/app/tasks/page.js
"use client";

import React, { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import Navbar from "@/components/Navbar"; // Import Navbar component

const TasksPage = () => {
  const router = useRouter();
  const [tasks, setTasks] = useState([]);
  const [title, setTitle] = useState("");
  const [description, setDescription] = useState("");
  const [status, setStatus] = useState(""); // New status state
  const [error, setError] = useState("");
  const [user, setUser] = useState(null);

  useEffect(() => {
    // Check if the user is logged in
    const fetchUserData = async () => {
      const res = await fetch("http://localhost:5001/api/users/check-auth", {
        method: "GET",
        credentials: "include",
      });
      const data = await res.json();
      if (res.ok) {
        setUser(data);
        fetchTasks(); // Fetch tasks if user is authenticated
      } else {
        router.push("/login"); // Redirect to login if not authenticated
      }
    };

    fetchUserData();
  }, [router]);

  const fetchTasks = async () => {
    try {
      const res = await fetch("http://localhost:5001/api/tasks", {
        method: "GET",
        credentials: "include",
      });
      const data = await res.json();
      if (res.ok) {
        setTasks(data); // Set tasks in state
      } else {
        setError(data.message || "Failed to load tasks");
      }
    } catch (err) {
      setError("Something went wrong while fetching tasks");
    }
  };

  const handleCreateTask = async (e) => {
    e.preventDefault();
    try {
      const res = await fetch("http://localhost:5001/api/tasks", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        credentials: "include",
        body: JSON.stringify({ title, description, status }), // Include status in request body
      });
      const data = await res.json();

      if (res.ok) {
        setTitle("");
        setDescription("");
        setStatus(""); // Clear status input after creation
        fetchTasks(); // Refresh tasks after creation
      } else {
        setError(data.message || "Failed to create task");
      }
    } catch (err) {
      setError("Something went wrong while creating the task");
    }
  };

  const handleDeleteTask = async (taskId) => {
    try {
      const res = await fetch(`http://localhost:5001/api/tasks/${taskId}`, {
        method: "DELETE",
        credentials: "include",
      });
      const data = await res.json();

      if (res.ok) {
        fetchTasks(); // Refresh tasks after deletion
      } else {
        setError(data.message || "Failed to delete task");
      }
    } catch (err) {
      setError("Something went wrong while deleting the task");
    }
  };

  return (
    <div>
      {user && <Navbar user={user} />}{" "}
      {/* Render Navbar if user is available */}
      <div className="max-w-4xl mx-auto p-6">
        <h2 className="text-3xl font-bold mb-6">Your Tasks</h2>

        {/* Create Task Form */}
        <form onSubmit={handleCreateTask} className="space-y-4 mb-6">
          <div>
            <Label htmlFor="title">Title</Label>
            <Input
              id="title"
              type="text"
              value={title}
              onChange={(e) => setTitle(e.target.value)}
              required
            />
          </div>
          <div>
            <Label htmlFor="description">Description</Label>
            <Input
              id="description"
              type="text"
              value={description}
              onChange={(e) => setDescription(e.target.value)}
              required
            />
          </div>
          <div>
            <Label htmlFor="status">Status</Label>
            <Input
              id="status"
              type="text"
              value={status}
              onChange={(e) => setStatus(e.target.value)} // Handle status input
              required
            />
          </div>

          <Button type="submit">Create Task</Button>
        </form>

        {/* Display Tasks */}
        {error && <p className="text-red-600">{error}</p>}
        <div className="space-y-4">
          {tasks.map((task) => (
            <div
              key={task.id}
              className="bg-gray-200 p-4 rounded-md flex justify-between items-center"
            >
              <div>
                <h3 className="font-semibold">{task.title}</h3>
                <p>{task.description}</p>
                <p>Status: {task.status}</p> {/* Display the task status */}
              </div>
              <div className="space-x-2">
                <Button
                  onClick={() => handleDeleteTask(task.id)}
                  className="bg-red-500 text-white hover:bg-red-600"
                >
                  Delete
                </Button>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default TasksPage;
